@import '../../../styles/mixins.scss';

.root {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
}

.searchContainer {
  padding: 20px 40px;
  width: 40%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.map {
  width: inherit;
  height: 100%;
  z-index: 1;
  padding: 20px;
  border-radius: 4px;
  margin-right: 32px;
  position: relative;
}
